
.my-tunes {
  display: inline-block;
  min-height: 500px;
  min-width: 50%;
  background: green;
}
.itunes {
  display: inline-block;
  background: red;
  min-height: 500px;
  min-width: 45%;
}
.playlist-baby {
  position: fixed;
  max-height: 800px;
  overflow-y: auto;
  overflow-x: hidden;
}
.tasty-jams {
  color: white;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}



